.header{
    background-color: #131921;
    height: 60px;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}

.header__logo{
    width: 100px;
    object-fit: contain;
    margin: 18px 20px 0 20px;
    cursor: pointer;
}

.header__search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}

.header__searchInput{
    height: 12px;
    padding: 15px;
    border: none;
    width: 100%;
    outline: none;
}

.header__searchIcon{
    padding: 3px;
    height: 30px !important;
    background-color: #cd9042;
    cursor: pointer;
}

.header__nav{
    display: flex;
    justify-content: space-evenly;
}

.header__option{
    display: flex;
    flex-direction: column;
    margin: 0 10px;
    color: #fff;
}

.header__optionLineOne{
    font-size: 10px;
    opacity: 0.8;
}

.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket{
    display: flex;
    align-items: center;
    color: #fff;
}

.header__basketCount{
    margin: 0 10px;
}

